import http from './http-common'
import authHeader from './auth-header'

const getAll = (data) => {
    return http.post("/workorder/all", { data: data, headers: authHeader() })
}

const getWO_NUMs = () => {
    return http.get("/workorder/wo_nums")
}

const getWOBySN = sn => {
    return http.get(`/workorder/onebysn/${sn}`)
}

export default {
    getAll,
    getWO_NUMs,
    getWOBySN
} 